#app {
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-weight: 100;
  text-align: center;
  
}

#app h1 {
  font-weight: 100;
  text-align: center;
  padding: 20px;
  font-size: 2.5em;
  color: #777777;
}
#app input {
  width: 300px;
  display: inline-block;
  margin: 0 auto;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid #ccc;
  font-size: 16px;
  font-weight: 100;
} 

#app input:focus {
  outline: none;
  -webkit-box-shadow: 0px 0px 2px #ccc;
          box-shadow: 0px 0px 2px #ccc;
}

#app label {
  display: block;
  width: 300px;
  text-align: left;
  margin: 5px auto;
  color: #b3b3b3;  
}

#app button {
  display: block;
  margin: 10px auto;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #645a6d;
  background: #645a6d;
  border-radius: 3px;
  color: #fff;
  width: 300px; 
}

#app button:focus {
  outline: none;
}


